  /**
   * „Ç™„Éï„É©„Ç§„É≥ÂêåÊúü„Ç≠„É•„Éº„ÇíÂá¶ÁêÜ
   */
  async processOfflineQueue() {
    try {
      const offlineQueue = JSON.parse(
        localStorage.getItem('offlineWorkoutQueue') || '[]'
      );

      if (offlineQueue.length === 0) {
        return { synced: 0, failed: 0 };
      }

      let synced = 0;
      let failed = 0;

      for (const item of offlineQueue) {
        try {
          await this.saveWorkout(item.data);
          this.removeFromOfflineQueue(item.id);
          synced++;
        } catch (error) {
          console.error(`Failed to sync ${item.id}:`, error);
          failed++;
        }
      }

      return { synced, failed };
    } catch (error) {
      console.error('Failed to process offline queue:', error);
      throw error;
    }
  }

  /**
   * „Ç™„Éï„É©„Ç§„É≥„Ç≠„É•„Éº„Åã„Çâ„Ç¢„Ç§„ÉÜ„É†„ÇíÂâäÈô§
   */
  removeFromOfflineQueue(id) {
    try {
      const offlineQueue = JSON.parse(
        localStorage.getItem('offlineWorkoutQueue') || '[]'
      );
      const filteredQueue = offlineQueue.filter((item) => item.id !== id);
      localStorage.setItem(
        'offlineWorkoutQueue',
        JSON.stringify(filteredQueue)
      );
    } catch (error) {
      console.error('Failed to remove from offline queue:', error);
    }
  }

  /**
   * „Ç™„Éï„É©„Ç§„É≥„Ç≠„É•„Éº„Å´„Ç¢„Ç§„ÉÜ„É†„ÇíËøΩÂä†
   */
  addToOfflineQueue(data) {
    try {
      const offlineQueue = JSON.parse(
        localStorage.getItem('offlineWorkoutQueue') || '[]'
      );
      const item = {
        id: data.id || `offline_${Date.now()}`,
        data,
        timestamp: new Date().toISOString(),
        retryCount: 0,
      };

      offlineQueue.push(item);
      localStorage.setItem('offlineWorkoutQueue', JSON.stringify(offlineQueue));
    } catch (error) {
      console.error('Failed to add to offline queue:', error);
    }
  }

  /**
   * Ë™çË®ºÁä∂ÊÖã„ÅÆÂ§âÊõ¥„ÇíÁõ£Ë¶ñ
   */
